.btn-nav {
  position: absolute;
  top: 50%;
  right: 35px;
  display: none;
  transform: translate(50%, -50%);
  width: 34px;
  height: 24px;
  cursor: pointer;
  z-index: 10;
  background: none;
}

@media only screen and (max-width: 767px) {
  .btn-nav {
    display: block;
  }
}

.btn-nav span {
  position: absolute;
  display: block;
  width: 28px;
  height: 3px;
  background-color: #fff;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  transition: .3s;
  transition-delay: .3s;
}

.btn-nav span:before {
  position: absolute;
  display: block;
  content: '';
  width: 100%;
  height: 100%;
  background-color: #fff;
  top: -8px;
  left: 0;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.2);
  border-radius: 2px;
  transition-property: top , transform;
  transition-delay: 0.3s, 0s;
  transition-duration: 0.3s, 0.3s;
}

.btn-nav span:after {
  position: absolute;
  display: block;
  content: '';
  width: 100%;
  height: 100%;
  background-color: #fff;
  top: 8px;
  left: 0;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.2);
  border-radius: 2px;
  transition-property: top , transform;
  transition-delay: 0.3s, 0s;
  transition-duration: 0.3s, 0.3s;
}

.btn-nav.is-active span:before {
  top: 0;
  transform: rotate(45deg);
  transition-delay: 0s, 0.3s;
}

.btn-nav.is-active span:after {
  top: 0;
  transform: rotate(-45deg);
  transition-delay: 0s, 0.3s;
}

.btn-nav.is-active span {
  background: transparent;
  box-shadow: 0 2px 2px transparent;
}

