.faq {
	.title-m {
		margin-bottom: 15px;
		@include sm {
			margin-bottom: 10px; } }
	&__title {
		font-weight: 600;
		margin-bottom: 50px;
		@include sm {
			margin-bottom: 25px; } }
	p {
		margin-bottom: 0;
		font-size: 18px;
		font-weight: 400;
		line-height: 1.31;
		color: $mine-shaft;
		@include sm {
			font-size: 14px;
			margin-bottom: 20px; }
		&:not(:last-child) {
			margin-bottom: 20px; } }
	&__info {
		margin-top: 60px;
		font-size: 18px;
		font-weight: 400;
		line-height: 1.31;
		color: $mine-shaft;
		p {
			margin-bottom: 0; }
		@include sm {
			margin-top: 25px;
			font-size: 14px;
			p {
				margin-bottom: 0; } } } }
