.products {
	&__inner {
		max-width: 1010px;
		padding: 0 20px;
		margin: 0 auto;
		@include sm {
			padding: 0 15px; } }
	&__wrap {
		display: flex;
		justify-content: space-between;
		padding-top: 125px;
		@include md {
			display: block; }
		@include sm {
			padding-top: 60px; } }
	&__col {
		width: 50%;
		padding: 0 40px; }
	&__slider {
		max-width: 478px;
		margin-right: 10px;
		@include md {
			margin-right: 0;
			max-width: 100%;
			margin-bottom: 60px; }
		@include sm {
			margin-bottom: 30px; } }
	&__info {
		max-width: 415px;
		margin-left: 10px;
		@include md {
			margin-left: 0;
			max-width: 100%; } }
	&__title-l {
		margin-bottom: 15px; }
	&__subtitle {
		font-size: 27px;
		line-height: 1.79;
		margin-bottom: 26px;
		color: $light-gray;
		font-weight: 400;
		@include sm {
			font-size: 18px;
			margin-bottom: 13px; } }
	&__description {
		font-size: 20px;
		line-height: 1.59;
		margin-bottom: 38px;
		color: $light-gray;
		font-weight: 400;
		@include sm {
			font-size: 13px;
			margin-bottom: 20px; } }
	&__size {
		color: $night-blue;
		font-weight: 700;
		line-height: 1.69;
		font-size: 17px;
		@include sm {
			font-size: 15px; } }
	&__size-value {
		color: $light-gray;
		font-size: 20px;
		line-height: 1.48;
		font-weight: 400;
		margin-bottom: 16px;
		@include sm {
			font-size: 18px;
			margin-bottom: 20px; } }
	&__form-title {
		font-size: 17px;
		color: $night-blue;
		font-weight: 700;
		line-height: 2.08;
		margin-bottom: 15px;
		@include sm {
			font-size: 15px; } }
	&__form {
		@include md {
			max-width: 100%; } } }

.slider-products {
	max-width: 443px;
	margin: 0 auto 81px;
	@include sm {
		margin-bottom: 40px; }
	&__row:not(:last-child) {
		margin-bottom: 65px;
		@include sm {
			margin-bottom: 30px; } }
	&__row:last-child {
		.slick-slide {
			padding: 0 6px; }
		.slick-slider {
			margin: 0 -6px; } }
	&__item {
		height: 470px;
		position: relative;
		@include sm {
			height: 290px; }
		iframe {
			width: 100%;
			height: 100%;
			position: absolute;
			left: 0;
			top: 0; } }
	&__item-in {
		background-size: contain;
		background-repeat: no-repeat;
		background-position: 50% 50%;
		position: absolute;
		left: 0;
		top: 0;
		bottom: 0;
		right: 0; } }

.foto-small {
	height: 100%;
	position: relative;
	width: 76px;
	height: 76px;
	border: 2px solid #e8e7e7;
	cursor: pointer;
	@include sm {
		height: 56px; }
	.slick-slide.slick-current & {
		border: 2px solid $blue; }
	img {
		max-width: 100%;
		display: block; }
	&__inner {
		position: absolute;
		background-size: contain;
		background-position: 50% 50%;
		background-repeat: no-repeat;
		left: 0;
		top: 0;
		bottom: 0;
		right: 0; } }

.block-item {
	background: $lilyWhite;
	border: 1px solid $blueZodiac;
	position: relative;
	padding: 25px 20px 25px 0;
	@include sm {
		padding: 25px 10px; }
	&__logo {
		max-width: 147px;
		position: absolute;
		top: -37%;
		left: 20px;
		display: block;
		@include md {
			position: relative;
			display: block;
			left: 50%;
			transform: translateX(-50%); }
		img {
			max-width: 100%;
			display: block; } }
	&__text {
		font-size: 15px;
		font-weight: 400;
		line-height: 1.59;
		margin-left: 185px;
		max-width: 270px;
		letter-spacing: -0.02em;
		color: $light-gray;
		@include md {
			max-width: 100%;
			margin-left: 0;
			text-align: center; }
		@include sm {
			font-size: 10px; } } }
.select {
	.selection {
		text-align: left; }
	.select2-selection .select2-selection--single {
		font-size: 14px;
		font-weight: 400;
		line-height: 1.2;
		padding: 11px 15px 8px;
		color: $light-gray;
		width: 100%;
		background-color: $black-squeeze;
		border-radius: 4px;
		outline: none; }
	.select2-container--default .select2-selection--multiple, .select2-container--default .select2-selection--single {
		border: none;
		font-size: 14px;
		font-weight: 400;
		width: 100%;
		background-color: $black-squeeze;
		border-radius: 4px;
		outline: none; }
	.select2-container--default .select2-selection--single .select2-selection__rendered {
		color: $light-gray;
		line-height: 37px; } }
.select2-search--dropdown {
	display: none; }
.select2-container .select2-selection--single {
	height: 37px;
	border: 1px solid  $black-squeeze; }
.select2-container--default .select2-selection--single .select2-selection__arrow {
	height: 35px;
	right: 10px; }
.select2-container--default .select2-selection--single .select2-selection__arrow b {
	background: url('../img/arrow-bottom.svg') no-repeat;
	border-color: transparent;
	border-style: none;
	width: 10px;
	height: 7px;
	background-size: cover; }
.select2-container--default .select2-selection--single {
	border 1px solid  $black-squeeze {} }
.select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow b {
	transform: rotate(180deg); }
.select2-dropdown {
    color: $light-gray;
    font-size: 14px;
    font-weight: 400;
    overflow: hidden;
    border: 1px solid  $light-gray;
    border-radius: 4px; }
.select2-container--default .select2-results__option--highlighted[aria-selected] {
	background-color: $white;
	color: $light-gray;
	font-size: 14px;
	font-weight: 400; }
.select2-container--default .select2-results__option[aria-selected=true] {
	background-color: $night-blue;
	color: $white; }
.select2-container .select2-selection--single .select2-selection__rendered {
	padding-left: 15px; }
.select2-container--default.select2-container--open.select2-container--below .select2-selection--multiple, .select2-container--default.select2-container--open.select2-container--below .select2-selection--single {
	border: 1px solid  $light-gray;
	border-bottom: none; }
.select2-results__option {
	padding-left: 15px; }
.select2-container--default.select2-container--open.select2-container--above .select2-selection--single {
	border: 1px solid  $light-gray;
	border-top: none; }
