.about {
	&__bg {
		position: relative; }
	&__image {
		max-width: 700px;
		height: 0;
		padding-bottom: 20%;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: 50% 50%;
		margin: 0 auto;
		z-index: 1;
		&:after {
			position: absolute;
			content: '';
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(#1e8bf0,1);
			z-index: -1; }
		&:before {
			position: absolute;
			content: '';
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(#1e8bf0,.5);
			z-index: 2; } }
	&__wrap {
		max-width: 700px;
		margin: 0 auto; }
	&__title {
		color: $gray-blue;
		margin: 0 0 40px;
		@include sm {
			margin: 40px 0 20px; } }
	&__description {
		font-size: 22px;
		line-height: 1.4;
		font-weight: 400;
		color: $light-gray;
		@include sm {
			font-size: 14px; } }
	&__video {
		max-width: 100%;
		height: 393px;
		margin: 100px 0;
		@include sm {
			height: 200px;
			margin: 40px 0 20px; }
		@include xs {
			height: 164px; }
		iframe {
			background-color: transparent;
			vertical-align: text-bottom;
			position: relative;
			width: 100%;
			height: 100%;
			min-width: 100%;
			min-height: 100%;
			max-width: 100%;
			max-height: 100%;
			overflow: hidden;
			display: block; } } }


