body {
	color: $black;
	background: $white;
	font-family: $opensans;
	@include font-smoothing;
	@include f(18, 24);
	transition: transform .3s ease-in-out;
	overflow-x: hidden;
	position: relative;
	min-width: 320px;
	&.is-active {
		position: fixed;
		overflow-x: visible;
		top: 0;
		left: 0;
		right: 0;
		transform: translateX(-270px); } }

.out {
	overflow: hidden; }

.inner {
	position: relative;
	margin: 0 auto;
	padding: 0 15px;
	max-width: 1170px;
	@include clr; }

.title-l {
	font-size: 41px;
	line-height: 1.2;
	font-weight: 400;
	letter-spacing: -0.02em;
	color: $night-blue;
	text-transform: uppercase;
	font-weight: 700;
	@include sm {
		font-size: 20px; } }

.wrap {
	padding: 84px 0;
	@include sm {
		padding: 35px 0 0; } }


.title-lg {
	font-size: 36px;
	font-weight: 400;
	line-height: (43 / 36);
	letter-spacing: -0.02em;
	color: $white; }

.title-md {
	font-size: 32px;
	font-weight: 400;
	line-height: 1.28;
	color: $white;
	letter-spacing: -0.02em;
	@include sm {
		font-size: 18px; }
	&_gray {
		color: $gray-blue; } }

.title-m {
	font-size: 22px;
	font-weight: 500;
	line-height: 1.31;
	letter-spacing: -0.02em;
	color: $gray-blue;
	@include sm {
		font-size: 16px; } }

.title-xs {
	font-size: 14px;
	font-weight: 400;
	line-height: 1.77;
	letter-spacing: -0.02em;
	color: $indigo; }

.title-sm {
	font-size: 12px;
	font-weight: 700;
	line-height: 1.334;
	letter-spacing: -0.02em;
	text-transform: uppercase; }

.btn {
	display: inline-block;
	background: #9cddfa;
	border-radius: 3px;
	padding: 18px 38px;
	color: $gray-blue;
	font-size: 16px;
	font-weight: 600;
	text-transform: uppercase;
	letter-spacing: -0.01em;
	min-width: 245px;
	line-height: 1.5;
	@include sm {
		padding: 9px 19px;
		font-size: 14px;
		line-height: 20px;
		min-width: 170px; } }

.link {
	color: $indigo;
	transition: border-bottom .3s;
	border-bottom: 1px solid $white;
	&:hover {
		border-bottom: 1px solid $indigo; } }

