.shakers {
	&__top {
		display: flex;
		max-width: 788px;
		padding: 80px 15px 75px;
		margin: 0 auto;
		@include sm {
			flex-direction: column;
			align-items: center;
			padding: 40px 15px 37px; } }
	&__title {
		padding-top: 35px; }
	&__img {
		width: 58.36%;
		@include sm {
			order: -1; }
		img {
			width: 100%;
			display: block; } }
	&__in {
		max-width: 901px;
		padding: 0 15px;
		margin: 0 auto; }
	&__content {
		display: flex;
		justify-content: space-between;
		margin-bottom: 92px;
		@include r(871) {
			display: block;
			margin-bottom: 40px;
			text-align: left; } }
	&__content-mob {
		display: none;
		@include r(870) {
			display: block;
			margin-bottom: 40px;
			text-align: left; } }
	&__col {
		position: relative;
		@include r(870) {
			margin-bottom: 20px; } }
	&__subtitle {
		margin-bottom: 12px; }
	&__button-group {
		border: 1px solid $blue;
		font-size: 0;
		border-radius: 3px;
		overflow: hidden;
		position: relative;
		@include r(870) {
			position: absolute;
			opacity: 0;
			visibility: hidden;
			z-index: 10;
			margin-top: -1px;
			transition: opacity 0.5s, visibility 0.5s;
			border: 1px solid #1e8bf0;
			border-radius: 0 0 3px 3px;
			border: 1px solid $blue;
			font-size: 0;
			width: 100%;
			top: 100%;
			.is-open & {
				opacity: 1;
				visibility: visible; } } }
	&__button {
		color: $blue;
		background: white;
		overflow: hidden;
		padding: 11px 10px 11px;
		font-size: 10px;
		line-height: 10px;
		@include r(870) {
			width: 100%;
			text-align: left; }
		&.is-checked {
			background: $blue;
			color: $white;
			padding: 11px 13px; } }
	&__button-mob {
		display: none;
		@include r(870) {
			padding: 11px 10px;
			font-size: 10px;
			line-height: 10px;
			display: block;
			width: 100%;
			text-align: left;
			position: relative;
			border: 1px solid $blue;
			border-radius: 3px;
			color: $blue;
			background: white;
			padding: 11px 10px 11px;
			.is-open & {
				border-radius: 3px 3px 0 0; }
			.is-checked & {
				background: $blue;
				color: $white;
				padding: 11px 13px; }
			&:after {
				content: '';
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				right: 10px;
				@include arr(10,5, $blue, b);
				.is-open & {
					@include arr(10,5, $blue, t); } } } }
	&__inner {
		margin: 0 -40px; } }

.grid {
	margin: 0 auto;
	&__col {
		padding: 0 30px;
		margin-bottom: 60px;
		height: 304px;
		width: 190px;
		@include sm {
			margin-bottom: 20px; } } }

.item {
	text-align: center;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: flex-end;
	height: 100%;
	&__info {
		width: 100%; }
	&__img {
		margin-bottom: 23px;
		max-width: 105px;
		display: flex;
		align-items: flex-end;
		img {
			max-width: 100%;
			display: block; } }
	&__title {
		font-size: 15px;
		line-height: 15px;
		color: $night-blue;
		font-weight: 400;
		letter-spacing: -0.08em;
		margin-bottom: 3px; }
	&__capacity {
		font-size: 14px;
		line-height: 17px;
		font-weight: 400;
		letter-spacing: -0.01em;
		color: $light-gray; } }



